isplayerExternalData>;\nonMounted(() => {\n    treeView = fileDisplayerUseCase.createFileExplorerClone(rootDiv.value!, {\n        cb: {\n            select: (item) => {\n                switch (item.externalData.objectType) {\n                    case \"application\": case \"folder\": case \"module\": default: return;\n                    case \"model\": break;\n                }\n                props.selectCb(item.externalData);\n                emit(\"close\");\n            }\n        }, displayFilterer: displayModelDict[props.type],\n    });\n});\n\nonUnmounted(() => {\n    treeView.reset();\n});\n\n</script>\n\n<template>\n    <div class=\"container\">\n        <div ref=\"rootDiv\"></div>\n    </div>\n</template>\n\n<style scoped>.container {\n    padding: 10px;\n}</style>"],"sourceRoot":""}]);
